<template>
	<div class="CommodityInfoEntry">
		<header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span>商品信息录入</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div>
				<div>
					<span>商品代码</span>
					<input  class="readonly_input" readonly  v-model="CommodityCode"></input>
				</div>
				<div>
					<span>客户代码</span>
					<input  class="readonly_input"  readonly  v-model="CustomerCode"></input>
				</div>
			</div>
			<div class="goodsName">
				<span>商品名称</span>
				<textarea  class="readonly_input" v-model="CommodityName"   readonly></textarea>
			</div>
			<div>
				<span>标准重量</span>
				<input ref="StandardWeight" @keyup.enter="getFocus('StandardVolume')" v-model="StandardWeight"    @focus="g_focus('StandardWeight')"></input>
				<span>g</span>
				<i v-if="StandardWeight" @click="clearInput('StandardWeight')"  class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>标准体积</span>
				<input  ref="StandardVolume"   @keyup.enter="getFocus('LengthWidthHeight')" v-model="StandardVolume"   @focus="g_focus('StandardVolume')"></input>
				<span>cm³</span>
				<i v-if="StandardVolume" @click="clearInput('StandardVolume')"  class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>长*宽*高</span>
				<input ref="LengthWidthHeight"  @keyup.enter="submit" v-model="LengthWidthHeight"   @focus="g_focus('LengthWidthHeight')"></input>
				<span>cm</span>
				<i v-if="LengthWidthHeight" @click="clearInput('LengthWidthHeight')"  class="iconfont icon-cuowu"></i>
			</div>
			<p class="positionFixed"><span class="g_singleBtn" @click="submit">确定</span></p>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent  class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p  @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"",
		data(){
			return{
				tipsHide:false,//提示信息是否出现
				msg:"",//提示信息内容
				CommodityCode:"",//商品代码
				CustomerCode:"",//客户代码
				CommodityName:"",//商品名称
				StandardWeight:"",//标准重量
				StandardVolume:"",//标准体积
				LengthWidthHeight:"",//长*宽*高
			}
		},
		created(){

			this.CommodityCode = this.$route.query.sku;//商品代码
			this.CustomerCode = this.$route.query.storerkey;//客户代码
			this.CommodityName = this.$route.query.descr;//商品名称
			this.StandardWeight = this.$route.query.stdcube;//标准重量
			this.StandardVolume = this.$route.query.stdgrosswgt;//标准体积
			this.LengthWidthHeight = this.$route.query.susr10;//长*宽*高
		},
		mounted() {
			this.$refs['StandardWeight'].select();
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
		},
		methods:{
			// 返回上一页
			goPrev:function(){

				var that = this;

				this.$router.push({
					name:"ScanCommodityCode",
					query:{
						input_CommodityCode: that.CommodityCode
					}
				})
			},
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				
				this[inputName] = qrcode;
				
				if(inputName == "StandardWeight") {

					this.getSelect('StandardVolume');

				}else if(inputName == "StandardVolume") {

					this.getSelect('input_LibraryCode');

				}else if(inputName == "LengthWidthHeight") {

					this.submit();
				}
				
			},
			// 确定 按钮点击事件
			submit(){

				var  that = this;

				if(!this.StandardWeight){
					
					that.msg = "请填写标准重量";

					that.inputFocus = "StandardWeight";

					that.tipsShow("StandardWeight");

					return;
				}else if(!this.StandardVolume){
					
					that.msg = "请填写标准体积";

					that.inputFocus = "StandardVolume";

					that.tipsShow("StandardVolume");

					return;
				}else if(!this.LengthWidthHeight){
					
					that.msg = "请填写长*宽*高";

					that.inputFocus = "LengthWidthHeight";

					that.tipsShow("LengthWidthHeight");

					return;
				}

				// 商品信息录入接口
				var SkuInfoUrl = that.HOST + "RFReceiveSkuInfo.do";

				var SkuInfo_Data = {
					storerkey : that.CustomerCode,//客户代码
					whseid : that.$store.state.WarehouseCode,//仓库 code
					sku : that.CommodityCode,//商品代码
					stdgrosswgt : that.StandardVolume,//标准体积
					stdcube : that.StandardWeight, //标准重量
					susr10 : that.LengthWidthHeight,//长*宽*高
				}

				that.httpRequest(SkuInfoUrl,SkuInfo_Data,function(result){

					if(result.data.data == "1"){

						// 获取最近的一个收货批次
						var LottableUrl = that.HOST + "RFReceiptSkuLottable.do";

						var Lottable_Data = {
							ReceiptKey : that.$store.state.Receipt_SubmitData[0].receiptkey,//收货单号
							UserID : that.$store.state.UserID,//用户名
							Sku : that.CommodityCode//商品代码
						}

						that.httpRequest(LottableUrl,Lottable_Data,function(resData){
					
							if(resData.data.data.length){
									
								that.$store.state.Receipt_PrevBatchProp = resData.data.data;
								
							}else{
								that.$store.state.Receipt_PrevBatchProp = [];
							}

							// RF收货接口
							var SummaryUrl = that.HOST + "RFReceiveSku.do";

							var sum_Data = {
								ReceiptKey : that.$store.state.Receipt_SubmitData[0].receiptkey,//收货单号
								StorerKey : that.$store.state.Receipt_SubmitData[0].storerkey,//客户代码
								Sku : that.CommodityCode//商品代码
							}

							that.httpRequest(SummaryUrl,sum_Data,function(res){

								console.log(res.data.data)

								if(res.data.data.length){
										
									that.$store.state.RFReceiveSkuData = res.data.data;

									that.$router.push({
										name:"TakeDelivery",
										query:{
											Sku : that.CommodityCode//商品代码
										}
									})
									
								}else{
									that.tipsShow();

									that.inputFocus = "CommodityCode";

									that.msg = "商品在收货单中不存在";
								}
							},'CommodityCode')
						})

					}else{
						that.tipsShow();

						that.inputFocus = "StandardWeight";

						that.msg = "保存失败，请重新提交";
					}
				},"StandardWeight")
			}
		}
	}
</script>
<style lang="less">
	.CommodityInfoEntry{
		.container{
			padding-top: 15/50rem;
			&>div{
				width:100%;
				height: 60/50rem;
				box-sizing: border-box;
				margin-bottom: 15/50rem;
				overflow: hidden;
				position: relative;
				&>div{
					width:375/50rem;
					float: left;
					span{
						width: 130/50rem;
						margin-left: 10/50rem;
						float: left;
						text-align: left;
					}
					input{
						width:220/50rem;
						float: left;
						height:55/50rem;
						padding-left: 5/50rem;
						box-sizing: border-box;
					}
				}
				.icon-cuowu{
					position:absolute;
					right:115/50rem;
					font-size: 36/50rem;
					top: 8/50rem;
				}
				&>span{
					width: 130/50rem;
					height: 55/50rem;
					line-height: 55/50rem;
					float: left;
					margin-left: 10/50rem;
					text-align: left;
				}
				&>span:nth-child(3){
					width:80/50rem;
				}
				&>input{
					width:520/50rem;
					height:55/50rem;
					float: left;
					padding: 0 10/50rem;
					box-sizing: border-box;
				}
			}
			&>.goodsName{
				height:170/50rem;
				margin-bottom: 15/50rem;
				overflow: hidden;
				&>span{
					width: 130/50rem;
					text-align: left;
					float: left;
				}
				&>textarea{
					width:595/50rem;
					height:170/50rem;
					float:left;
					outline:none;
					font-size: 29/50rem;
					box-sizing: border-box;
				}
			}
			.readonly_input{
				background-color: #F0F0F0;
				border:none;
			}
			.readonly_input:focus{
				outline: none;
			}
		}
	}

    @media screen and (max-width: 329px) and (min-width: 291px) {
   		.CommodityInfoEntry{
			.container{
				&>div{
					height: 80/50rem;
					line-height: 80/50rem;
					&>div{
						span{
							width: 150/50rem;
							height: 65/50rem;
							line-height: 65/50rem;
						}
						input{
							width: 200/50rem;
							height:65/50rem;
							padding:0 10/50rem;
						}
					}
					.icon-cuowu{
						font-size:40/50rem;
						top: -5/50rem;
					}
					&>span{
						width: 150/50rem;
						height: 65/50rem;
						line-height: 65/50rem;
					}
					input{
						height:65/50rem;
						width: 490/50rem;
					}
				}
				&>.goodsName{
					margin-bottom: 5/50rem;
					margin-top: 5/50rem;
					&>span{
						width: 150/50rem;
					}
					&>textarea{
						width: 575/50rem;
						padding:0 10/50rem;
						height: 150/50rem;
					}
				}
				p.positionFixed{
					height:85/50rem;
				}
			}
		}
    }
	
    @media screen and (max-width: 290px){
    	.CommodityInfoEntry{
			.container{
				font-size: 40/50rem;
				&>div{
					height: 80/50rem;
					&>div{
						span{
							width: auto;
							height: 75/50rem;
							line-height: 75/50rem;
							margin-left: 0;
						}
						input{
							width:200/50rem;
							height:75/50rem;
						}
					}
					.icon-cuowu{
						right: 130/50rem;
						font-size:50/50rem;
						top:10/50rem;
					}
					&>span{
						margin-left: 0;
						text-align: left;
						width:auto;
						height: 75/50rem;
						line-height: 75/50rem;
						margin-right: 10/50rem;
					}
					&>input{
						width:480/50rem;
						height:75/50rem;
					}
				}
				&>.goodsName{
					margin-bottom: 10/50rem;
					&>span{
						width:auto;
						margin-right: 0;
					}
					&>textarea{
						width: 575/50rem;
						font-size: 25/50rem;
						padding:0 10/50rem;
						margin-left: 0;
					}
				}
			}
		}
    }
</style>


